import React, { Component } from 'react';
import BaseApp from './screens/App';
import { BrowserRouter } from 'react-router-dom'

import './App.css';

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <BaseApp />
      </BrowserRouter>
    );
  }
}

export default App;



