import React from 'react';
import { Link } from 'react-router-dom'
import Icon from './icon.js';

const IconBoxDefaultProps = {
	url: '/',
}

function IconBox({icon, title, price, className, url, ...other}) {
	return (
		<Link to={url}>
			<span>
				<div
					className={`iconBox ${className}`}
					{...other}>
					<Icon icon={icon} size={4} color="white" />
					<h3>{title}</h3>
					<p>from {price}</p>
				</div>
			</span>
		</Link>
	);
}

IconBox.defaultProps = IconBoxDefaultProps;

export default IconBox;


