import React, { Component } from 'react';
import Geolocated from './geo.js';
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';

import './lockout.css';

export class MapContainer extends Component {
  constructor(props) {
      super(props);
      this.state = {
        showingInfoWindow: false,
        activeMarker: {},
        selectedPlace: {},
        coords: {
        }
      }
  }
  onMarkerClick = () => {

  }
  onMapClicked = () => {

  }

  onCoordChange = (coords) => {
    this.setState({
      coords,
    })
  }
render() {
  const pos = {lat: this.state.coords.latitude, lng: this.state.coords.longitude};

    return (
      <div>
        <Geolocated onCoordChange={this.onCoordChange} />
        { this.state.coords.latitude && this.state.coords.longitude ?
          <Map google={this.props.google} zoom={17} initialCenter={pos} >
   
          <Marker onClick={this.onMarkerClick}
                  name={'Current location'}
                   />
   
          <InfoWindow onClose={this.onInfoWindowClose}>
              <div>
                <h1>{this.state.selectedPlace.name}</h1>
              </div>
          </InfoWindow>
        </Map> :
        <span>Loading Map</span>
      }
      </div>
    );
  }
}

export default GoogleApiWrapper({
  apiKey: ('AIzaSyD-yFn1Ikwk5lPYXMhWXLL4y5d8gbj1t_A')
})(MapContainer)


